<template>
  <div>
    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="'services'">
        Services
      </v-tab>
      <v-tab :key="'networking'" v-if="setting.networking">
        Networking
      </v-tab>
      <v-tab :key="'hostInfo'">
        Host Info
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="'services'">
        <service-items></service-items>
      </v-tab-item>
      <v-tab-item :key="'networking'" v-if="setting.networking">
        <networking></networking>
      </v-tab-item>
      <v-tab-item :key="'hostInfo'">
        <host-info></host-info>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import ServiceItems from './ServiceItems';
import Networking from './Networking';
import HostInfo from './HostInfo';

export default {
  name: "services",
  components: {
    ServiceItems,
    Networking,
    HostInfo,
  },
  data:()=>({
    tab: 'services',
    isDisabled:false
  }),
   computed: {
    setting () {
      return this.$store.state.setting;
    }
  },
};
</script>




