<template>
  <div style="padding:20px">
            <v-row>
              <v-col class="text-left">
                <v-btn x-small class="text-left" color="primary" @click="dialogIframe = true; currentIframe = {show: true}" >
              Add
              <v-icon small>mdi-plus</v-icon>
            </v-btn>
              </v-col>
            </v-row>
            <v-data-table :headers="iframeHeaders" :items="iframes">
              <template v-slot:item.tools="{item}">
                <v-btn @click="() => clickDeleteIframe(item)" icon small color="error" class="white--text">
                  <v-icon small dark >
                    mdi-delete
                  </v-icon>
                </v-btn>
                <v-btn @click="() => clickUpdateIframe(item)" icon small color="success" class="white--text">
                  <v-icon small dark >
                    mdi-rename-box
                  </v-icon>
                </v-btn>
              </template>
            </v-data-table>
            <v-dialog v-model="dialogIframe" max-width="500px">
              <iframe-form :onSave="onSaveIframe" :onCancel="onCancelIframe" :item="currentIframe"></iframe-form>
            </v-dialog>
  </div>
</template>

<script>
import axios from "../axios";
import IframeForm from './IframeForm';
export default {
    name: "View",
    components: {
        IframeForm
    },
    data(){
      return{
          currentIframe: {},
          dialogIframe:false,
          iframeHeaders: [
            {
              text: 'Name',
              align: 'start',
              value: 'name',
            },
            { text: 'Icon', value: 'icon' },
            { text: 'Url', value: 'url' },
            { text: 'Tools', value: 'tools' },
        ],
      };
    },
    computed: {
      iframes () {
        return this.$store.state.iframes;
      },
    },
    methods: {
      getIframes() {
        axios.get('iframe').then(r => {
          this.$store.commit('changeIframes', r);
        })
        },
      clickDeleteIframe(data) {
        this.$store.commit('changeLoading', true);
        axios.delete(`iframe/${data._id}`).then(r => {
          this.$store.commit('changeLoading', false);
          this.$toasted.show('Success!');
          this.getIframes();
        }).catch(({message}) => {
          this.$store.commit('changeLoading', false);
          this.$toasted.show(message, {type: 'error'});
        })
      },
      clickUpdateIframe(data) {
        this.currentIframe = Object.assign({}, data);
        this.dialogIframe= true;
      },
      onSaveIframe(data) {
        if(Object.isExtensible(data.icon)){
            Object.assign(data,{icon:data.icon.name});
        }
        this.$store.commit('changeLoading', true);
        const method = data._id ? 'put': 'post';
        axios[method]('iframe', data).then(r => {
          this.$store.commit('changeLoading', false);
          this.$toasted.show('Success!');
          this.dialogIframe = false;
          this.getIframes();
        }).catch(({message}) => {
          this.$store.commit('changeLoading', false);
          this.$toasted.show(message, {type: 'error'});
        })
      },
      onCancelIframe() {
          this.dialogIframe = false;
      },
    }
}
</script>

<style>

</style>


