<template>
  <v-card>
        <v-card-title>
          <span class="headline">Update Bacnet Server Info</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.ip" label="Ip" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.port" label="Port" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.device_id" label="Device Id" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.model_name" label="Model Name" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.local_obj_name" label="Local Obj Name" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.vendor_name" label="Vendor Name" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.vendor_id" label="Vendor Id" required :rules="[rules.required]"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
          <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(formData)"> Save </v-btn>
        </v-card-actions>
      </v-card>
</template>

<script>

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
    }),
    computed: {
      formData () {
        return this.item;
      },
    },
  };
</script>



