<script>
import {wiresPlatPort} from "../utils/utils";
const port = wiresPlatPort();
import axios from "../axios";
import DeviceDetailForm from "./DeviceDetailForm";
import { copyToClipboard,isJsonString } from "../utils/utils";
import logo from '../serviceicons/nube-frog-green.png';
export default {
  components: {DeviceDetailForm},
  data: () => ({
    logo,
    deviceDialog: false,
    confirmDialog:false,
    action:'',
    currentState:'',
    wiresStatus: {},
    smonStatus: {},
    bacRestStatus: {},
    bacServerStatus: {},
    bbbStatus: {},
    lorawanStatus: {},
    mosquittoStatus: {},
    dracStatus: {},
    bacnetInfo: {},
    bacnetServerStatus: {},
    valid: false,
    platform: {},
    platformData: {},
    test:{},
    bacRestData:{},
    ping:{},
    modbusDialog:false,
    rules: {
      required: (value) => !!value || "Required.",
    },
    importDialog: false,
    dataImport: ""
  }),
  async mounted() {
    this.getData();
    this.getPlatform();
    if(Object.keys(this.pingData)!==0){
      this.getBacRestData(this.pingData);
    }
  },

  computed: {
    setting () {
      return this.$store.state.setting;

    },
    pingData(){
      return this.$store.state.pingData;
    }
  },
  watch: {
    '$store.state.pingData':function(val){
        console.log('val',val);
      }
  },
  methods: {
    closeBacRestDialog(){
      this.modbusDialog=false;
    },
    getBacRestData(data){
      const arrData=Object.keys(data);
      arrData.forEach(e=>{
        if(Object.isExtensible(data[e])){
           if(Array.isArray(data[e])){
             data[e].forEach(i=>{
               Object.assign(data,i);
             })
           }
           Object.assign(data,data[e]);
        }
      })
      const newData=Object.keys(data);
      const newValueData=Object.values(data);
      for(let i = 0; i < newValueData.length; i++){
        if(!Object.isExtensible(newValueData[i])){
          const tempObj = new Object();
          tempObj[newData[i]]=newValueData[i];
          Object.assign(this.bacRestData,tempObj);
        }
      }
    },
    onClickEditDevice() {
      this.deviceDialog = true;
      this.platformData = Object.assign({}, this.platform);
    },
    clickModbusDialog(){
      if(Object.keys(this.pingData).length!==0){
        this.modbusDialog=true;
        return;
      }
      this.$toasted.show('Point Server is not running',{type:"error"});
    },
    closeDialog(){
      this.confirmDialog=false;
      this.action='',
      this.currentState=''
    },
    clickDialog(service,state){
      this.action=service;
      this.currentState=state;
      this.confirmDialog=true;
    },
    getData() {
      this.getServiceData();
      this.getBacnetInfo();
      this.getBacnetStatus();
    },
    getPlatform() {
      axios.get(`s-mon/plat`).then((data) => {
        this.platform = data;
      }).catch(e => {
        this.$toasted.show(e.message, {type: 'error'});
      });
    },
    async handleSavePlat() {
      axios.put(`s-mon/plat`, this.platformData).then(({data}) => {
        this.deviceDialog = false;
        this.$toasted.show("success");
        this.getPlatform();
      }).catch(({message}) => {
        this.deviceDialog = false;
        this.$toasted.show(message, {type: 'error'});
      });
    },
    onCancelDeviceDialog() {
      this.deviceDialog = false;
      this.getPlatform();
    },
    async getServiceData() {
      await this.getServiceStatus('WIRES', 'wiresStatus');
      await this.getServiceStatus('BAC_REST', 'bacRestStatus');
      await this.getServiceStatus('BAC_SERVER', 'bacServerStatus');
      await this.getServiceStatus('BBB', 'bbbStatus');
      await this.getServiceStatus('LORAWAN', 'lorawanStatus');
      await this.getServiceStatus('MOSQUITTO', 'mosquittoStatus');
      await this.getServiceStatus('DRAC', 'dracStatus');
    },
    async getBacnetInfo() {
      this.bacnetInfo = await axios.get(`bac-net/server-info`);
    },
    async getBacnetStatus() {
      // this.bacnetServerStatus = await axios.get(`bac-net/server-status`);
      axios.get(`bac-net/server-status`).then(value => {
        this.bacnetServerStatus = value;
      }).catch(({message}) => {
        this.$toasted.show(message, {type: 'error'});
      });
    },
    async getServiceStatus(service, state) {
      const result = await axios.get(`s-mon/service-stats/${service}`);
      if(result && result.msg){
        this[state] = result.msg;
      }
    },
    async callService(action, service) {
      this.$store.commit('changeLoading', true);
      axios.post(`s-mon/call-service`, {action, service})
        .then(r => {
          this.$toasted.show('success');
          this.$store.commit('changeLoading', false);
          this.confirmDialog=false;
          this.action='';
          this.currentState='';
        })
        .catch(err => {
          this.$toasted.show(err.message, {type: 'error'});
          this.$store.commit('changeLoading', false);
          this.confirmDialog=false;
          this.action='';
          this.currentState='';
        });
    },
    clickImport(){
      this.importDialog = true;
    },
    closeDialogImport() {
      this.importDialog = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },
    async saveImport() {
      if(!isJsonString(this.dataImport)){
        return this.$toasted.show("incorrect data type!", {type: 'error'});
      }
      this.platformData = JSON.parse(this.dataImport);
      this.closeDialogImport();
      this.deviceDialog = true;
    },
    clickJsonExport(){
      const data = _.cloneDeep(this.platform);
      data.device_id = null;
      const status = copyToClipboard(JSON.stringify(data));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
  },
};
</script>
<style scoped>
.value-style{
  /* color: #666666; */
  font-weight: bold;
  /* max-width: 120px;; */
}
.card-height{
  height: 250px;
}
.v-card__actions{
  position: relative;
  height: 6rem;
}
.v-card__subtitle{
  height: 60px;
}
.services-btn{
  margin: 2%;
}
.btns-row{
  padding: 16px;
}
@media only screen and (max-width: 600px) {
  .v-card__title{
      width: 369.2px;
  }
}


</style>




