<template>
  <div>
    <h1>UPDATE SERVICE - BACnet Server</h1>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step :complete="e1 > 1" step="1">Release version</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="e1 > 2" step="2">Service status</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="e1 > 3" step="3">Download</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="e1 > 4" step="4">Install</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="e1 > 5" step="5">Start</v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-radio-group v-model="selectedId">
            <v-radio
              v-for="r in releases"
              :key="r.id"
              :label="r.name"
              :value="r.id"
            ></v-radio>
          </v-radio-group>

          <v-btn
            color="primary"
            @click="e1 = 2"
          >
            Continue
          </v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <div style="text-align: left">
            Service: nubeio-bacnet-server.service <br />
            Status: {{serviceStatus.status ? 'Running': 'Stopped'}} <br />
          </div>
          <v-btn @click="e1 = 1">Back</v-btn>
          <v-btn @click="getServiceStatus" color="success">Reload</v-btn>
          <v-btn @click="stopService()" color="primary">Continue</v-btn>
        </v-stepper-content>

        <v-stepper-content step="3">
          <div style="text-align: left">
            Name: <a target="_blank" :href="selectedRelease.html_url">{{selectedRelease.name}}</a> <br />
            Author: <strong>{{selectedRelease.author.login}}</strong> <br />
            Created at: {{selectedRelease.created_at | formatDate}} <br />
            <a target="_blank" :href="selectedRelease.zipball_url">Source code (zip)</a> <br />
            <a target="_blank" :href="selectedRelease.tarball_url">Source code (tar.gz)</a> <br />
          </div>
          <v-btn @click="e1 = 2">
            Back
          </v-btn>
          <v-btn @click="download" color="primary">
            Download
          </v-btn>
        </v-stepper-content>
        <v-stepper-content step="4">
          <div style="text-align: left">
            Install Service
          </div>
          <v-btn @click="e1 = 3">
            Back
          </v-btn>
          <v-btn @click="install" color="primary">
            Install
          </v-btn>
        </v-stepper-content>
        <v-stepper-content step="5">
          <div style="text-align: left">
            Start Service
          </div>
          <v-btn @click="e1 = 4">
            Back
          </v-btn>
          <v-btn @click="callService('start')" color="primary">
            Start
          </v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
    <v-overlay :value="overlay">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
  </div>
</template>

<script>
import axios from "../axios";

export default {
  data() {
    return {
      e1: 1,
      releases: [],
      serviceStatus: {},
      selectedId: '',
      selectedRelease: {author: {}},
      overlay: false
    };
  },
  mounted() {
    this.getReleaseList();
    this.getServiceStatus();
  },
  methods: {
    async install() {
      this.$store.commit('changeLoading', true);
      const data = {
        service: "BAC_SERVER"
      };
      axios.post(`s-mon/install-service`, data).then(result => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show('Install completed')
        this.e1 = 5;
      }).catch(({message}) => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(message, {type: 'error'});
      });
    },
    async download() {
      this.$store.commit('changeLoading', true);
      const data = {
        service: "BAC_SERVER",
        build_url: this.selectedRelease.zipball_url
      };
      axios.post(`s-mon/download-service`, data).then(r => {
        this.$toasted.show('Download completed');
        this.$store.commit('changeLoading', false);
        this.e1 = 4;
      }).catch(({message}) => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(message, {type: 'error'});
      });
    },
    async getServiceStatus() {
      this.$store.commit('changeLoading', true);
      axios.get(`s-mon/up-service/BAC_SERVER`).then(result => {
        this.$store.commit('changeLoading', false);
        this.serviceStatus = result;
      }).catch(({message}) => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(message, {type: 'error'});
      });
    },
    async stopService() {
      if (!this.serviceStatus.status) {
        this.e1 = 3;
        return;
      }
      await this.callService('stop');
      this.e1 = 3;
    },
    async callService(action) {
      this.$store.commit('changeLoading', true);
      axios.post(`s-mon/call-service`, {action, service: 'BAC_SERVER'}).then(result => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(`${action} success!`);
      }).catch(({message}) => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(message, {type: 'error'});
      });
    },
    getReleaseList() {
      this.$http.get('https://api.github.com/repos/NubeIo/rubix-bacnet-server/releases').then(({data}) => {
        const releases = data.slice(0, 20);
        this.releases = releases;
        this.selectedId = releases[0].id;
        this.selectedRelease = releases[0];
      });
    }
  },
  watch: {
    selectedId: function(val, oldVal) {
      this.selectedRelease = this.releases.find(r => r.id === val);
    }
  }
};
</script>



