<template>
  <v-card>
      <v-card-title>
        <span class="headline">Iframe</span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-row>
              <v-col>
                <v-text-field v-model="formData.name" label="Name" required :rules="[rules.required]"></v-text-field>
              </v-col>

            </v-row>
            <v-row>
              <v-col>
                <v-text-field v-model="formData.url" label="Url" required :rules="[rules.required]"></v-text-field>
              </v-col>
              <v-col>
                <v-select v-model="formData.type" label="Type" :items="['linkOnly', 'iframeOnly', 'linkAndIframe']" required :rules="[rules.required]"></v-select>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4">
                <v-checkbox label="Show?" v-model="formData.show" />
              </v-col>
              <v-col cols="12" md="8">
                <v-combobox
                  v-model="formData.icon"
                  :loading="loading"
                  :items="icons"
                  :search-input.sync="search"
                  label="Select Icon"
                  item-text="name"
                  item-value="name"
                >
                  <template slot="item" slot-scope="{item}">
                    <v-icon dark> {{ `mdi-${item.name}` }} </v-icon> <v-spacer></v-spacer>{{item.name}}
                  </template>
                </v-combobox>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
        <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(formData)"> Save </v-btn>
      </v-card-actions>
    </v-card>
</template>

<script>
  import axios from "../axios";

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
      loading: false,
      icons: [],
      search: null,
      select: null,
      typingText:''
    }),
    watch: {
      search (val) {
        val && val !== this.formData.icon && this.getIcons(val)
      },
      'item.icon': function(val) {
        if (val) {
          this.getIcons(val);
        }
      }
    },
    computed: {
      formData () {
        return this.item;
      },
    },
    methods: {
      getIcons (v) {
        this.loading = true;
        axios.get('icons', {params: {keyword: v}}).then(r => {
          this.icons = r;
          this.loading = false;
        });
      }
    },
  };
</script>



