<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="items"
      class="elevation-1"
      item-key="uuid"
      show-select
      disable-sort
    >

      <template v-slot:top >
        <v-toolbar flat>
          <v-toolbar-title>BACNET SERVER</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="getItems" x-small color="primary" class="white--text" >
              Refresh
              <v-icon x-small right dark> mdi-refresh </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="dialog = true; currentItem = null" x-small color="success" class="white--text" >
              Create
              <v-icon x-small right dark> mdi-plus </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text">
              Delete
              <v-icon x-small right dark > mdi-delete </v-icon>
            </v-btn>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(items,'csv')">CSV</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(items, 'xlsx')">Excel</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-btn @click="clickImport" x-small color="primary" class="white--text" >
              Import
              <v-icon x-small right dark> mdi-import </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" >
              Export
              <v-icon x-small right dark> mdi-export </v-icon>
            </v-btn>
            <v-btn class="config-server" color="primary"  x-small @click="bacnetdialog=true">
                Configure Server
            </v-btn>
          </div>
          <v-spacer></v-spacer>

        </v-toolbar>
      </template>

      <template v-slot:item.actions="{ item }" >
        <v-icon small class="mr-2" @click="editItem(item)" color="primary">
          mdi-pencil
        </v-icon>
        <v-icon small @click="clickDelete(item)" color="error">
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:no-data> No data </template>
    </v-data-table>
    <!-- Bacnet Info -->
    <v-dialog v-model="bacnetdialog" max-width="500px">
      <bacnet-server-form :item="bacnetInfo" :onCancel="onBacNetCancel" :onSave="onBacNetSave"></bacnet-server-form>
    </v-dialog>
    <!-- Delete dialog -->
    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog" max-width="500px">
      <point-form :item="currentItem" :onCancel="onCancel" v-if="dialog" :onSave="onSave"></point-form>
    </v-dialog>

    <!-- Import dialog-->
    <v-dialog v-model="dialogImport" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-textarea
              outlined
              name="input-7-4"
              label="import data"
                v-model="dataImport"
            ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveImport" :disabled="!dataImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import PointForm from './PointForm';
import BacnetServerForm from "./BacnetServerForm";
import { exportCSV, isJsonString, copyToClipboard, exportExcel } from "../utils/utils";
import axios from "../axios";

export default {
  components: {
    PointForm,
    BacnetServerForm
  },
  data: () => ({
    version: 1,
    dialogDelete: false,
    action: "",
    selected: [],
    dialog: false,
    bacnetdialog:false,
    dialogImport: false,
    bacnetInfo:{},
    dataImport: "",
    items: [],
    headers: [
      { text: "Actions", value: "actions" },
      { text: "Object Name", value: "object_name",  },
      { text: "Object Type", value: "object_type",  },
      { text: "Address", value: "address", },
      { text: "Present Value ",value:"point_store.present_value"},
      { text: "Present Value Last Update",value:"point_store.ts"},
      { text: "Relinquish Default", value: "relinquish_default", },
      { text: "Description", value: "description",  },
      { text: "Data Offset", value: "data_offset",  },
      { text: "Data Round", value: "data_round",  },
      { text: "Enable",value: "enable" },
      { text: "Event State",value: "event_state" },
      // { text: "Fault",value: "fault" },
      { text: "Units", value: "units",align:"center" },
      { text: "Priority Array Write _1",value:"priority_array_write._1",align:"start"},
      { text: "Priority Array Write _2",value:"priority_array_write._2",align:"start"},
      { text: "Priority Array Write _3",value:"priority_array_write._3",align:"start"},
      { text: "Priority Array Write _4",value:"priority_array_write._4",align:"start"},
      { text: "Priority Array Write _5",value:"priority_array_write._5",align:"start"},
      { text: "Priority Array Write _6",value:"priority_array_write._6",align:"start"},
      { text: "Priority Array Write _7",value:"priority_array_write._7",align:"start"},
      { text: "Priority Array Write _8",value:"priority_array_write._8",align:"start"},
      { text: "Priority Array Write _9",value:"priority_array_write._9",align:"start"},
      { text: "Priority Array Write _10",value:"priority_array_write._10",align:"start"},
      { text: "Priority Array Write _11",value:"priority_array_write._11",align:"start"},
      { text: "Priority Array Write _12",value:"priority_array_write._12",align:"start"},
      { text: "Priority Array Write _13",value:"priority_array_write._13",align:"start"},
      { text: "Priority Array Write _14",value:"priority_array_write._14",align:"start"},
      { text: "Priority Array Write _15",value:"priority_array_write._15",align:"start"},
      { text: "Priority Array Write _16",value:"priority_array_write._16",align:"start"},
      { text: "Created On",value:"created_on"},
      { text: "Updated On",value: "updated_on" },
      { text: "UUID", value: "uuid", align: "start" }
    ],
  }),
  watch: {
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  async mounted() {
    const { setting } = this.$store.state;
    this.getData();
    this.getItems();
  },
  methods: {
     getData() {
      this.getBacnetInfo();
    },
    editItem(item) {
      this.currentItem = item;
      this.dialog = true;
    },
    onCancel() {
      this.dialog = false;
    },
    onBacNetCancel(){
      this.bacnetdialog=false;
    },
     async getBacnetInfo() {
      this.bacnetInfo = await axios.get(`bac-net/server-info`);
    },
    async getBacnetStatus() {
      this.bacnetServerStatus = await axios.get(`bac-net/server-status`);
    },
    async onBacNetSave(data) {
      await axios.patch(`bac-net/server-info`, data).then(r => {
        this.dialog = false;
        this.$toasted.show('Success');
        this.getBacnetInfo();
      }).catch(e => {
        this.$toasted.show(e.message, {type: 'error'});
        this.dialog = false;
      });
    },
    async onSave(data) {
      let postData = {...data};
      const method = data.uuid ? 'patch': 'post';
      let url = `bac-net/point`;
      if (data.uuid) {
        url += '/' + data.uuid;
        delete postData.uuid;
      }
      try{
        await axios[method](url, postData);
        this.dialog = false;
        await this.getItems();
        this.$toasted.show("success");
      }catch(e){
        this.$toasted.show(e.message, {type: 'error'});
      }

    },

    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.items.indexOf(this.currentItem);
      this.items.splice(this.editedIndex, 1);
      await axios.delete(`bac-net/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    async getItems() {
      const { points } = await axios.get( `bac-net/point` );
      points.forEach(e=>{
          const keys=Object.keys(e.priority_array_write);
          const values=Object.values(e.priority_array_write);
          for(let i=0;i<keys.length;i++){
            if(values[i]===null){
                e.priority_array_write[keys[i]]='null'
            }
          }
      });
      this.items = points;
    },
    async saveImport() {
      if(!isJsonString(this.dataImport)){
        return this.$toasted.show("incorrect data type!", {type: 'error'});
      }
      const data = _.cloneDeep(JSON.parse(this.dataImport));
      if(data && data.length > 0){
        for (const item of data) {
          await this.saveImportSingle(item)
        }
      }else{
        await this.saveImportSingle(data);
      }
      this.closeDialogImport();
      this.selected = [];
    },
    async saveImportSingle(point){
      delete point.uuid;
      this.version = 1;
      point.object_name = this.getNotExistingElm(point.object_name, 'object_name');
      point.address = this.getNotExistingElm(point.address, 'address');
      await this.onSave(point);
    },
    clickImport(){
      this.dialogImport = true;
      this.dataImport = JSON.stringify(this.selected[0]);
    },
    clickDownload(array, type){
      const data = this.convertExportData(array);
      type === 'csv' ? exportCSV("points", data) : exportExcel("points", data);
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    convertExportData(array){
      const data = _.cloneDeep(array).map((point)=>{
        _.forIn(point, (point_value, point_key) => {
          if(point_key != 'point_store' && point_key != 'priority_array_write'){
              const pointKey = `point.${point_key}`;
              point = {...point, [pointKey] : point_value}
          }else{
            _.forIn(point[point_key], (sub_value, sub_key) => {
              const subKey = `point.${point_key}.${sub_key}`;
              point = {...point, [subKey] : sub_value}
            });
            delete point[point_key];
          }
        });
        return point;
     });
      return data;
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },

    getNotExistingElm(data, key) {
      const result = key === 'object_name'? `${data}_${this.version}` : data + this.version;
      const existingItem = this.items.find(item =>item[key] === result);
      if (!existingItem) {
        return result;
      }
      this.version++;
      return this.getNotExistingElm(data, key);
    }
  }
};
</script>
<style scoped>
  .config-server{
    position:absolute;
    right: 1em;
  }
  @media only screen and (max-width: 600px) {
      .config-server{
          bottom: -3em;
          left: 8.2rem;
      }
  }
</style>



