import Vue from 'vue';
import Router from 'vue-router';
import CropImage from '../components/CropImage';
import Login from "../components/Login";
import File from "../components/File";
import Iframe from "../components/Iframe";
import MainLayout from "../components/MainLayout";
import Networks from "../components/Networks";
import Devices from "../components/Devices";
import Points from "../components/Points";
import UpdateWires from "../components/UpdateWires";
import UpdatePointServer from "../components/UpdatePointServer";
import UpdateBacnet from "../components/UpdateBacnet";
import Settings from "../components/Settings";
import Services from "../components/Services";
import BacNetServer from "../components/BacNetServer";
import Exception403 from "../components/Exception403";
import LoRaNetwork from "../components/LoRaNetwork";

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [
    { path: '/login', name: 'login', component: Login, meta: { guest: true } },
    {
      path: '', name: 'main',
      component: MainLayout,
      meta: { auth: true },
      children: [
        { path: 'services', name: 'services', component: Services, meta: { settingKey: 'services' } },
        { path: 'file', name: 'file', component: File, meta: { settingKey: 'fileManager' } },
        { path: 'modbus', name: 'modbus', component: Networks, meta: { settingKey: 'modbus' } },
        { path: 'bacnet-server', name: 'bacnet-server', component: BacNetServer, meta: { settingKey: 'bacnetServer' } },
        { path: 'devices', name: 'devices', component: Devices },
        { path: 'points', name: 'points', component: Points },
        { path: 'iframe/:id', name: 'wires', component: Iframe },
        { path: 'crop-image', name: 'Crop Image', component: CropImage },
        { path: 'update-wires', name: 'Update Wires', component: UpdateWires, meta: { settingKey: 'updateWires' } },
        { path: 'update-point-server', name: 'Point Server', component: UpdatePointServer, meta: { settingKey: 'updatePointServer' } },
        { path: 'update-bacnet', name: 'Update Bacnet', component: UpdateBacnet, meta: { settingKey: 'updateBacnet' } },
        { path: 'settings', name: 'Settings', component: Settings },
        { path: 'exception-403', name: 'exception-403', component: Exception403 },
        { path: 'loRa-network', name: 'loRa-network', component: LoRaNetwork },
        { path: '', redirect: 'services' },
      ]
    },
  ]
});

export default router



