<template>
   <div>
     <v-row>
      <v-col class="text-left" xl="3" lg="3" sm="12">
        <v-btn x-small color="primary" @click="clickImport" >
          Import
          <v-icon small>mdi-import</v-icon>
        </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small color="pink" @click="clickJsonExport">
          Export
          <v-icon small>mdi-export</v-icon>
        </v-btn>
      </v-col>
      <v-col xl="6" lg="6" sm="12">
        <h1>Settings</h1>
      </v-col>
    </v-row>
    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="'showHideSideBar'">
        SHOW/HIDE SIDEBAR
      </v-tab>
      <v-tab :key="'serviceSetting'">
        SERVICE SETTING
      </v-tab>
      <v-tab :key="'showHideService'">
        SHOW/HIDE SERVICE
      </v-tab>
      <v-tab :key="'apps'">
        APPS
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="'showHideSideBar'">
        <show-hide-sidebar></show-hide-sidebar>
      </v-tab-item>
      <v-tab-item :key="'serviceSetting'">
        <service-setting></service-setting>
      </v-tab-item>
      <v-tab-item :key="'showHideService'">
        <show-hide-service></show-hide-service>
      </v-tab-item>
      <v-tab-item :key="'apps'">
        <apps></apps>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import ShowHideSidebar from './ShowHideSidebar';
import ServiceSetting from './ServiceSetting';
import ShowHideService from './ShowHideService';
import Apps from './Apps';
export default {
  components:{
    ShowHideSidebar,
    ServiceSetting,
    ShowHideService,
    Apps
  },
  data:()=>({
    tab: 'showHideSidebar'
  }),
  computed: {
    setting () {
      return this.$store.state.setting;
    },
    loading () {
      return this.$store.state.loading;
    }
  },
  methods:{
    async onChangeImportData(){
      let result='through';
      if(!isJsonString(this.dataImport) || this.dataImport.length<0){
        this.isValidData=true;
      }
      else {
        const data=_.cloneDeep(JSON.parse(this.dataImport));
        const arrData=Object.keys(data);
        this.isValidData=true;
        if(arrData.length !== 0){
          for(let i=0;i<arrData.length;i++){
            if(!this.settingFields.includes(arrData[i])){
              result='notFound';
              break;
            }
          }
          this.isValidData=true;
          if(result==='through'){
            this.isValidData=false;
          }
        }
      }
      if(this.dataImport[0]===`[`||this.dataImport[this.dataImport.length-1]===`]`){
        this.disable=true;
      }
      if((this.dataImport[0]===`{` && this.dataImport[this.dataImport.length-1]===`}`)){
        this.disable=false;
      }
      this.disableSave=false;
      if(
        (this.disable!==this.isValidData)||
        (this.disable && this.isValidData)
      ) {
        this.disableSave=true;
      }
    },
  }
}
</script>

<style>

</style>


