<template>
  <v-card>
        <v-card-title>
          <span class="headline">Update LoRa Port</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.name" label="Name" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="formData.port" label="Port" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field type="number" v-model="formData.baud_rate" label="Baud Rate" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field type="number" v-model="formData.stop_bits" label="Stop Bits" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="formData.parity"
                    label="Parity"
                    :items="['O', 'E', 'N', 'Odd', 'Even']"
                    required :rules="[rules.required]"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field type="number" v-model="formData.byte_size" label="Byte Size" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field type="number" v-model="formData.timeout" label="Timeout" required :rules="[rules.required]"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
          <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(formData)"> Save </v-btn>
        </v-card-actions>
      </v-card>
</template>

<script>

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
    }),
    computed: {
      formData () {
        return this.item;
      },
    },
  };
</script>



