                            v-model="formData.units"
                            :items="units"
                            :label="'Units'"
                            persistent-hint
                            required :rules="[rules.required]"
                        >
                        </v-autocomplete>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="formData.description" label="Description" required :rules="[rules.required]"></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="formData.relinquish_default" label="Relinquish Default" type="number" required :rules="[rules.required]"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-select
                          v-model="formData.event_state"
