                      <v-col cols="12" md="3">
                        <v-text-field type="number" v-model="formData.priority_array_write._16" label="_16"></v-text-field>
                      </v-col>
                    </v-row>
                  </v-expansion-panel-content>
                </v-expansion-panel> -->
              </v-expansion-panels>


            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
          <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(formData)"> Save </v-btn>
        </v-card-actions>
      </v-card>
</template>

<script>
  import { units } from "../constants/appConstant";

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      expansionData: 0,
      valid: false,
      units
    }),
    computed: {
      formData () {
        return this.item ? { ...this.item }: { priority_array_write: {} };
      },
    },
  };
</script>



