<template>
  <div>
    <v-data-table
    v-model="selected"
    :headers="headers"
    :items="networks"
    class="elevation-1"
    item-key="uuid"
    show-select
    disable-sort
    :item-class="backgroundColor"  >
      <template v-slot:top >
        <v-breadcrumbs>..</v-breadcrumbs>
        <v-toolbar flat>
          <v-toolbar-title>NETWORKS</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="getNetworks" x-small color="primary" class="white--text" >
              Refresh<v-icon x-small right dark> mdi-refresh </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text">
              Delete<v-icon x-small right dark > mdi-delete </v-icon>
            </v-btn>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(networks,'csv')">CSV</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(networks, 'xlsx')">Excel</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-btn @click="clickImport" x-small color="primary" class="white--text" >
              Import<v-icon x-small right dark> mdi-import </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="clickJsonExport(selected)" :disabled="selected.length !== 1" x-small color="#009688" class="white--text" >
              Export<v-icon x-small right dark> mdi-export </v-icon>
            </v-btn>
          </div>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark small @click="dialog = true">
            Create Network
          </v-btn>
        </v-toolbar>
      </template>

      <template v-slot:item.name="{ item }">
        <a @click="onClickOpen(item)"> {{ item.name }} </a>
      </template>

      <template v-slot:item.enable="{ item }" >
        <v-checkbox v-model="item.enable" @click="saveEnable(item)"></v-checkbox>
      </template>

      <template v-slot:item.devices="{ item }" >
        <div>{{ item.devices ? item.devices.length : 0}}</div>
      </template>

      <template v-slot:item.actions="{ item }" >
        <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>&nbsp;
        <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>&nbsp;
        <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
      </template>
      <template v-slot:no-data> No data </template>
    </v-data-table>
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" label="Network name" required :rules="[rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.type" label="Type" :items="['RTU', 'TCP']" required :rules="[rules.required]"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="Enable" required></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.timeout" label="Timeout" required :rules="[rules.required]" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_timeout_global" label="Timeout global" required :rules="[rules.required]" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.point_timeout_global" label="Point timeout global" required :rules="[rules.required]" type="number"></v-text-field>
                </v-col>
              </v-row>
              <v-row v-if="editedItem.type === 'RTU'">
                 <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_parity" label="rtu parity" item-text="name" item-value="value" :items="rtu_paritys" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_speed" label="rtu speed" :items="[9600, 19200, 38400, 57600, 115200]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_stop_bits" label="rtu stopbits" :items="[1, 1.5, 2]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_byte_size" label="rtu bytesize" :items="[5, 6, 7, 8]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-combobox v-model="editedItem.rtu_port" :items="rtu_ports" label="rtu port" ></v-combobox>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogImport" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import Network</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-textarea
              outlined
              name="input-7-4"
              label="import data"
              v-model="dataImport"
            ></v-textarea>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveImport" :disabled="!dataImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete Network </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm">
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel } from "../utils/utils";
import axios from "../axios";

const pause = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

export default {

  data: () => ({
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    headers: [
      { text: "Actions", value: "actions",  },
      { text: "Name", value: "name", align: "start" },
      { text: "Fault", value: "fault",  },
      { text: "Type", value: "type",  },
      { text: "Enable", value: "enable",  },
      { text: "Timeout", value: "timeout",  },
      { text: "Device timout global", value: "device_timeout_global", },
      { text: "Point timeout global", value: "point_timeout_global", },
      { text: "Port", value: "rtu_port",  },
      { text: "Devices", value: "devices",  },
      { text: "UUID", value: "uuid", align: "start" },
    ],

    networks: [],
    rtu_paritys:[
      { name : "even", value: "E" },
      { name : "odd", value: "O" },
      { name : "none", value: "N" },
    ],
    rtu_ports:[ "/dev/ttyUSB0", "/dev/ttyUSB1", "/dev/ttyUSB2", "/dev/ttyUSB3", "/dev/ttyUSB4" ],
    currentItem: {},
    editedIndex: -1,
    editedItem: {
      name: "",
      type: "RTU",
      enable: false,
      timeout: 0,
      device_timeout_global: 0,
      point_timeout_global:0,
      rtu_port: "/dev/ttyUSB2",
      rtu_speed: 38400,
      rtu_stop_bits: 1,
      rtu_parity: 'N',
      rtu_byte_size: 8,
    },
    defaultItem: {
      name: "",
      type: "RTU",
      enable: false,
      timeout: 0,
      device_timeout_global: 0,
      point_timeout_global:0,
      rtu_port: "/dev/ttyUSB2",
      rtu_speed: 38400,
      rtu_stop_bits: 1,
      rtu_parity: 'N',
      rtu_byte_size: 8,
    },
    rules: { required: (value) => !!value || "Required." },
    action: "",
    objectKeys: Object.keys,
    objectValues: Object.values,

    dataImport: "",
    dialogImport: false,

  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Create Network" : "Edit Network";
    },
    pingData(){
      return this.$store.state.pingData;
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$store.state.pingData':function(val){
          this.getPingData(val)
      }
  },
  mounted() {
    this.getNetworks();
    if(Object.keys(this.pingData).length!==0){
      this.getPingData(this.pingData);
    }
  }, 
  methods: {
    getPingData(data){
      console.log(data);
      if(Object.keys(data).length===0){
          this.$toasted.show('Point Server is not running',{type:"error"});
          return;
      }
    },
    getNetworks() {
      axios.get( 'bac-rest/network' )
        .then((data) => {
          this.networks = data.networks;
        }).catch(({message}) => {
          this.$toasted.show(message, {type: 'error'});
        });
    },
    onClickOpen(item){
      this.$router.push({ name: "devices", query: { network_uuid: item.uuid } });
    },

    editItem(item) {
      this.editedIndex = this.networks.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    saveEnable(item){
      this.editedIndex = this.networks.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.save();
    },

    save() {
      let data = {
        name: this.editedItem.name,
        type: this.editedItem.type,
        enable: this.editedItem.enable,
        timeout: this.editedItem.timeout,
        device_timeout_global: this.editedItem.device_timeout_global,
        point_timeout_global: this.editedItem.point_timeout_global,
      };
      if(data.type == "RTU"){
        data = {
          ...data,
          rtu_byte_size: this.editedItem.rtu_byte_size,
          rtu_port: this.editedItem.rtu_port,
          rtu_speed: this.editedItem.rtu_speed,
          rtu_stop_bits: this.editedItem.rtu_stop_bits,
          rtu_parity: this.editedItem.rtu_parity,
        }
      }
      if (this.editedIndex > -1) {
        this.updateNetwork(data);
      } else {
        this.createNetwork(data);
      }
      this.close();
    },

    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.networks.indexOf(this.currentItem);
      this.networks.splice(this.editedIndex, 1);
      await axios.delete(`bac-rest/network/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },

    async confirm() {
      //function confirm delete, iam havent use api,
      await this[this.action]();
      this.dialog = false;
      this.action = "";
      // this.getNetworks();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDialogImport() {
      this.dialogImport = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },

    saveImport() {
      if(!isJsonString(this.dataImport)){
        return this.$toasted.show("incorrect data type!", {type: 'error'});
      }
      this.createNetwork(JSON.parse(this.dataImport));
      this.closeDialogImport();
      this.selected = [];
    },

    clickImport(){
      this.dialogImport = true;
      this.dataImport = JSON.stringify(this.selected[0]);
    },

    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected[0]));
      const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },

    clickDownload(array, type){
      const data = this.convertExportData(array);
      type === 'csv' ? exportCSV("networks", data) : exportExcel("networks", data);
    },
    convertExportData(array){
      const data = _.cloneDeep(array).map(network=>{
        _.forEach(network.devices, (device, indexD)=>{
          //devices
          _.forIn(device, (value, key) => {
            if(key != 'points'){
              const keyName = `device${indexD}.${key}`;
              network = {...network, [keyName] : value}
            }else{
              //points
              _.forEach(device.points, (point, indexP)=>{
                _.forIn(point, (point_value, point_key) => {
                  if(point_key != 'point_store'){
                      const pointKey = `device[${indexD}].point[${indexP}].${point_key}`;
                      network = {...network, [pointKey] : point_value}
                  }else{
                    //point_store
                    _.forIn(point.point_store, (point_store_value, point_store_key) => {
                      const pointStoreKey = `device[${indexD}].point[${indexP}].point_store.${point_store_key}`;
                      network = {...network, [pointStoreKey] : point_store_value}
                    });
                    delete point.point_store
                  }
                });
              });
              delete device.points
            }
          });
        });
        delete network.devices
        return network;
      });
      return data;
    },

    updateNetwork(data) {
      axios.put(`bac-rest/network/${this.editedItem.uuid}`, data )
        .then((response) => {
          const index = this.networks.findIndex(n=> n.uuid == response.uuid);
          Object.assign(this.networks[index], response);
          this.$toasted.show("update success");
        })
        .catch((error) => { this.$toasted.show("update fail", {type: 'error'}); });
    },

    createNetwork(data) {
      axios.post(`bac-rest/network`, data )
        .then((response) => {
          this.networks.push(response);
          if(data.devices && data.devices.length > 0){
            for (const device of data.devices) {
              device.network_uuid = response.uuid;
              this.createDevice(device);
            }
          }
          this.$toasted.show("create success");
        })
        .catch((error) => { this.$toasted.show("create fail", {type: 'error'}); });
    },

    createDevice(data) {
      axios.post( `bac-rest/device`, data )
        .then((response) => {
          const index = this.networks.findIndex(p => p.uuid == response.network_uuid);
          this.networks[index].devices.push(response);
          if(data.points && data.points.length > 0){
            for (const point of data.points) {
              point.device_uuid = response.uuid;
              this.createPoint(point);
            }
          }
        })
        .catch((error) => {});
    },

    createPoint(data) {
      axios.post( `bac-rest/point`, data ).then((response) => {}).catch((error) => { });
    },

    backgroundColor(item) {
      let classColor = '';
      if(item.fault){
        classColor = 'background-fault-true';
      }else{
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },

  },
};
</script>
<style>
  .background-fault-true{
    background-color: #fbb93e;
  }
  .background-enable-false{
    background-color: #666666;
  }

</style>





