<template>
  <div>
    <v-card>
      <v-card-title>
        <span class="headline">DEVICE DETAILS FORM</span>
        <v-spacer></v-spacer>
        <v-btn @click="generateUuid" class="generate-btn" small color="blue darken-2">Generate</v-btn>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-form v-model="valid" ref="form" lazy-validation>
              <v-row align="center" justify="center">
                <v-col cols="12" sm="12" md="6">
                  <v-text-field label="Device Id" v-model="formData.device_id" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="6">
                  <v-text-field label="Device Name" v-model="formData.device_name" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
              </v-row>
              <h3>Customer Details</h3>
              <v-row align="center" justify="center">
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Client Id" v-model="formData.client_id" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Client Name" v-model="formData.client_name" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
              </v-row>
              <h3>Device Location Address</h3>
              <v-row align="center" justify="center">
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Site Id" v-model="formData.site_id" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Site Name" v-model="formData.site_name" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field label="Street Address" v-model="formData.site_address" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="City/Town" v-model="formData.site_city" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="State" v-model="formData.site_state" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Zip/Postcode" v-model="formData.site_zip" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Country" v-model="formData.site_country" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
              </v-row>
              <v-row align="center" justify="center">
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Latitude" v-model="formData.site_lat" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Longitude" v-model="formData.site_lon" shaped filled required :rules="[rules.required]" ></v-text-field>
                </v-col>
              </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
        <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(formData)"> Save </v-btn>
      </v-card-actions>
    </v-card>

    <!--Confirm dialog-->
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Are you sure?
        </v-card-title>
        <v-card-text>Device Id = <b>{{uuid}}</b></v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="confirmDialog = false" >No</v-btn>
          <v-btn color="blue darken-1" text @click="onYes()">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
  import { copyToClipboard } from "../utils/utils";
  import axios from "../axios";

  export default {
    props: ['onSave', 'onCancel', 'item',],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
      confirmDialog: false,
      uuid: '',
    }),
    computed: {
      formData () {
        return this.item;
      },
    },
    methods:{
      async generateUuid() {
        this.uuid = await axios.get('generate-uuid');
        this.confirmDialog = true;
      },
      onYes(){
        this.formData.device_id = this.uuid;
        this.confirmDialog = false;
      },
    },
  };
</script>



