<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="items"
      class="elevation-1"
      item-key="uuid"
      show-select
      disable-sort
    >
      <template v-slot:top >
        <v-toolbar flat>
          <v-toolbar-title>LoRa Network</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="getItems" x-small color="primary" class="white--text" >
              Refresh
              <v-icon x-small right dark> mdi-refresh </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="dialog = true; currentItem = null" x-small color="success" class="white--text" >
              Create
              <v-icon x-small right dark> mdi-plus </v-icon>
            </v-btn>&nbsp;
            <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text">
              Delete
              <v-icon x-small right dark > mdi-delete </v-icon>
            </v-btn>&nbsp;
            <v-btn class="config-server" color="primary"  x-small @click="portDialog=true">
              Configure Server
            </v-btn>
          </div>
          <v-spacer></v-spacer>

        </v-toolbar>
      </template>

      <template v-slot:item.actions="{ item }" >
        <v-icon small class="mr-2" @click="editItem(item)" color="primary">
          mdi-pencil
        </v-icon>
        <v-icon small @click="clickDelete(item)" color="error">
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:no-data> No data </template>
    </v-data-table>
    <v-dialog v-model="portDialog" max-width="500px">
      <lo-ra-port-form :item="info" :onCancel="onPortCancel" :onSave="onPortSave"></lo-ra-port-form>
    </v-dialog>
    <!-- Delete dialog -->
    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog" max-width="500px">
      <sensor-form :item="currentItem" :onCancel="onCancel" v-if="dialog" :onSave="onSave"></sensor-form>
    </v-dialog>
  </div>
</template>

<script>
import SensorForm from './SensorForm';
import LoRaPortForm from "./LoRaPortForm";
import { exportCSV, isJsonString, copyToClipboard, exportExcel } from "../utils/utils";
import axios from "../axios";

export default {
  components: {
    LoRaPortForm,
    SensorForm,
  },
  data: () => ({
    version: 1,
    dialogDelete: false,
    action: "",
    selected: [],
    dialog: false,
    portDialog:false,
    dialogImport: false,
    info:{},
    dataImport: "",
    items: [],
    headers: [
      { text: "Actions", value: "actions" },
      { text: "Object Name", value: "object_name",  },
      { text: "Address", value: "address" },
      { text: "Sensor Type ",value:"sensor_type"},
      { text: "Sensor Model",value:"sensor_model"},
      { text: "Micro edge input type", value: "micro_edge_input_type", },
      { text: "Sensor wake up rate", value: "sensor_wake_up_rate",  },
      { text: "Description", value: "description",  },
      { text: "Enable", value: "enable",  },
      { text: "Fault",value: "fault" },
      { text: "Data round",value: "data_round" },
      { text: "Data offset",value:"data_offset"}
    ],
  }),
  watch: {
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  async mounted() {
    this.getInfo();
    this.getItems();
  },
  methods: {
    editItem(item) {
      this.currentItem = item;
      this.dialog = true;
    },
    async getInfo() {
      this.info = await axios.get(`lora/port`);
    },
    onCancel() {
      this.dialog = false;
    },
    onPortCancel(){
      this.portDialog=false;
    },
    async onPortSave(data) {
      await axios.patch(`lora/port`, data).then(r => {
        this.portDialog = false;
        this.$toasted.show('Success');
        this.getInfo();
      }).catch(e => {
        this.$toasted.show(e.message, {type: 'error'});
        this.portDialog = false;
      });
    },
    async onSave(data) {
      let postData = {...data};
      const method = data.uuid ? 'patch': 'post';
      let url = `lora/sensor`;
      if (data.uuid) {
        url += '/' + data.uuid;
        delete postData.uuid;
      }
      try{
        await axios[method](url, postData);
        this.dialog = false;
        await this.getItems();
        this.$toasted.show("success");
      }catch(e){
        this.$toasted.show(e.message, {type: 'error'});
      }
    },

    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.items.indexOf(this.currentItem);
      this.items.splice(this.editedIndex, 1);
      await axios.delete(`lora/sensor/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    async getItems() {
      const {sensors} = await axios.get( `lora/sensor` );
      this.items = sensors;
    },
  }
};
</script>
<style scoped>
.config-server{
  position:absolute;
  right: 1em;
}
@media only screen and (max-width: 600px) {
  .config-server{
    bottom: -3em;
    left: 8.2rem;
  }
}
</style>



