<template>
  <v-navigation-drawer v-model="drawer" :mini-variant.sync="mini" permanent class="float-left" id="drawer-menu" >
    <v-list-item class="px-3">
      <v-list-item-avatar style="padding: 3px">
        <v-img :src="logo" ></v-img>
      </v-list-item-avatar>
      <v-list-item-title>Rubix Platform</v-list-item-title>
      <v-btn icon @click.stop="mini = !mini">
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
    </v-list-item>

    <v-divider></v-divider>
    <v-list dense nav>
    <div v-for="item in items" :key="item.key" style="text-align: start;">
      <v-list-item v-if="!item.children" link :to="item.link" >
        <v-list-item-icon>
          <v-tooltip right>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-bind="attrs" v-on="on" > {{ item.icon }} </v-icon>
            </template>
            <span>{{ item.title }}</span>
          </v-tooltip>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-list-group v-if="item.children && item.children.length > 0" :key="item.key" no-action :prepend-icon="item.icon" >
        <template v-slot:activator>
          <v-list-item-content>
            <v-list-item-title v-text="item.title"></v-list-item-title>
          </v-list-item-content>
        </template>
        <v-list-item v-for="sub in item.children" :key="sub.title" link :to="sub.link" style="color: #FFFFFF;">
          <v-list-item-content>
            <v-list-item-title v-text="sub.title"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-group>
      </div>

      <v-list-item v-bind:key="iframe._id" v-for="iframe in iframes" link>
        <v-list-item-icon @click="onClickIframeItem(iframe)">
          <v-tooltip right>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-bind="attrs" v-on="on" > {{`mdi-${iframe.icon}`}} </v-icon>
            </template>
            <span>{{iframe.name}}</span>
          </v-tooltip>
        </v-list-item-icon>
        <v-list-item-content @click="onClickIframeItem(iframe)">
          <v-list-item-title  style="text-align: start">{{iframe.name}}</v-list-item-title>
        </v-list-item-content>
        <v-list-item-action v-if="iframe.type !== 'iframeOnly'">
          <v-icon @click="clickOnLinkIcon(iframe)" small color="grey lighten-1">mdi-open-in-new</v-icon>
        </v-list-item-action>
      </v-list-item>
      <v-list-item link :to="'/settings'">
        <v-list-item-icon >
          <v-tooltip right>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-bind="attrs" v-on="on" > mdi-settings </v-icon>
            </template>
            <span>Settings</span>
          </v-tooltip>
        </v-list-item-icon>
        <v-list-item-content >
          <v-list-item-title style="text-align: start">Settings</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-list-item style="position: absolute; bottom: 10px" @click="logout" class="logout">
        <v-list-item-icon>
          <v-icon>mdi-logout-variant</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title style="text-align: start" >Logout</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-list-item v-bind:style="css" @click="dialog=true" class="update-password" id="update-password">
        <v-list-item-icon>
          <v-icon>mdi-textbox-password</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title style="text-align:start" >Update</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <v-dialog v-model="dialog" max-width="500px">
      <update-password-form :item="password" :onCancel="onCancel" :onSave="onSave"></update-password-form>
    </v-dialog>
  </v-navigation-drawer>

</template>

<script>
import logo from "../assets/nube-frog-green.png";
import { sidebarItems } from '../constants/appConstant';
import UpdatePasswordForm from "./UpdatePasswordForm";

import axios from "../axios";
import * as _ from "lodash";

export default {
  components: {UpdatePasswordForm},
  props: ["drawer"],
  data: () => ({
    mini: true,
    logo,
    sidebarItems,
    password:{},
    passData:"",
    dialog:false,
    css:"",
    test:false
  }),
  updated(){
    this.css=this.mini?"position:absolute;bottom:50px;right:10px":"position:absolute;bottom:13px;right:10px";
  },
  methods: {
    onClickIframeItem({type, url, _id}) {
      if (type === 'linkOnly') {
        window.open(url,'_blank');
        return;
      }
      this.$router.replace(`/iframe/${_id}`)
    },
    clickOnLinkIcon({url}) {
      window.open(url,'_blank');
    },
    logout() {
      localStorage.removeItem("jwt");
      localStorage.removeItem("user");
      this.$router.push("login");
    },
    async onSave(data){
        if(data.newPassword.match("(?=.*?[A-Z])(?=.*?[0-9]){8,}")){
          axios.put('auth/update-password',data).then(res=>{
            this.$toasted.show("Changed Password Successfully",{type:"success"});
            this.logout();
          }).catch(({message}) => {
            if (Array.isArray(message)) {
              for (const m of message) {
                this.$toasted.show(m,{type: "error"});
              }
            } else {
              this.$toasted.show(message,{type: "error"});
            }
          });
        }
    },
    onCancel(){
      this.dialog=false;
      this.test=false;
    }
  },
  computed: {
    iframes () {
      const { iframes } = this.$store.state;
      return iframes.filter(i => i.show);
    },
    items () {
      const { setting } = this.$store.state;
      const menuItems = _.cloneDeep(sidebarItems);
      const result = [];
      for (const item of menuItems) {
        if (item.children) {
          item.children = item.children.filter(c=> setting[c.key]);
          if (item.children.length > 0) {
            result.push(item);
          }
        }
        if (setting[item.key]) {
          result.push(item);
        }
      }
      return result;
    }
  },

};
</script>
<style scoped>

</style>



