<template>
<div style="padding:20px">
  <v-row>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    General
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <img alt="logo" :src="logo">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col class="p-relative" sm="10">
                        <v-text-field label="Device user"  v-model="setting.deviceUser" :disabled="!enableDeviceUser"></v-text-field>
                        <span v-show="!setting.deviceUser" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableDeviceUser"></v-switch>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label="Build Dir" v-model="setting.buildDir" :disabled="!enableBuildDir" ></v-text-field>
                        <span v-show="!setting.buildDir" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableBuildDir"></v-switch>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label="Global Ip" v-model="setting.globalIp" :disabled="!setting.isGlobal" :rules="globalIpRule"></v-text-field>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="setting.isGlobal"></v-switch>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    Points Server
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <img alt="logo" :src="logo">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label=" Path" v-model="setting.bacRestPath" :disabled="!enableBacRestPath"></v-text-field>
                        <span v-show="!setting.bacRestPath" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableBacRestPath"></v-switch>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label="Lib Path" v-model="setting.bacRestLibPath" :disabled="!enableBacRestLibPath"></v-text-field>
                        <span v-show="!setting.bacRestLibPath" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableBacRestLibPath"></v-switch>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="4" class="p-relative">
                        <v-text-field :disabled="setting.isGlobal" label="Bac rest Ip" v-model="setting.bacRestIp"></v-text-field>
                        <span v-show="!setting.isGlobal && !setting.bacRestIp" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="6">
                        <v-text-field label="Bac rest port" v-model="setting.bacRestPort"  :disabled="!enableBacRestPort"></v-text-field>
                        <span v-show="!setting.bacRestPort" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableBacRestPort"></v-switch>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    BACnet Server
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <img alt="logo" :src="logo">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label="Path" v-model="setting.bacnetPath"  :disabled="!enableBacNetPath"></v-text-field>
                        <span v-show="!setting.bacnetPath" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableBacNetPath"></v-switch>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label="Lib Path" v-model="setting.bacnetLibPath"  :disabled="!enableBacNetLibPath"></v-text-field>
                        <span v-show="!setting.bacnetLibPath" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableBacNetLibPath"></v-switch>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="4" class="p-relative">
                        <v-text-field :disabled="setting.isGlobal" label="Bacnet Ip" v-model="setting.bacnetServerIp" ></v-text-field>
                        <span v-show="!setting.isGlobal && !setting.bacnetServerIp" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="6">
                        <v-text-field label="Bacnet port" v-model="setting.bacnetServerPort" :disabled="!enableBacNetPort"></v-text-field>
                        <span v-show="!setting.bacnetServerPort" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                          <v-switch v-model="enableBacNetPort"></v-switch>
                      </v-col>
                    </v-row>
                    <v-text-field style="visibility: hidden;"></v-text-field>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    Rubix Services
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <img alt="logo" :src="logo">
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col class="p-relative">
                        <v-text-field :disabled="setting.isGlobal" label="SMon Ip" v-model="setting.smonIp" ></v-text-field>
                        <span v-show="!setting.isGlobal && !setting.smonIp" class="required-noti">Required</span>
                      </v-col>
                    </v-row>

                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label="Port" v-model="setting.smonPort"  :disabled="!enableSmonPort"></v-text-field>
                        <span v-show="!setting.smonPort" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableSmonPort"></v-switch>
                      </v-col>
                    </v-row>
                    <v-text-field style="visibility: hidden;"></v-text-field>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    Wires
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <v-icon>mdi-transit-connection-variant</v-icon>
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label="Path" v-model="setting.wiresPath"  :disabled="!enableWiresPath"></v-text-field>
                        <span v-show="!setting.wiresPath" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableWiresPath"></v-switch>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col sm=4>
                <v-card outlined class="card-height">
                  <v-card-title>
                    LoRa
                    <v-row align="center" justify="end" >
                      <v-avatar size="35">
                        <v-icon>mdi-transit-connection-variant</v-icon>
                      </v-avatar>
                    </v-row>
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col class="p-relative">
                        <v-text-field label="LoRa Ip" v-model="setting.loraIp" :disabled="setting.isGlobal"></v-text-field>
                        <span v-show="!setting.isGlobal && !setting.loraIp" class="required-noti">Required</span>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="10" class="p-relative">
                        <v-text-field label="LoRa Port" v-model="setting.loraPort"  :disabled="!enableLoraPort"></v-text-field>
                        <span v-show="!setting.loraPort" class="required-noti">Required</span>
                      </v-col>
                      <v-col sm="2">
                        <v-switch v-model="enableLoraPort"></v-switch>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>

            <v-card-actions style="padding:0">
              <v-spacer></v-spacer>
              <v-btn :loading="loading" color="blue darken-2" @click="save">Save</v-btn>
            </v-card-actions>
</div>
</template>

<script>
import logo from "../serviceicons/nube-frog-green.png";
import axios from "../axios";
export default {
  data(){
    return{
      logo,
      globalIpRule:[],
      enableDeviceUser:false,
      enableBuildDir:false,
      enableBacRestPath:false,
      enableBacRestLibPath:false,
      enableBacNetPath:false,
      enableBacNetLibPath:false,
      enableBacNetPort:false,
      enableSmonPort:false,
      enableWiresPath:false,
      enableBacRestPort:false,
      enableLoraIp:false,
      enableLoraPort:false,
    }
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
    loading() {
      return this.$store.state.loading;
    },
  },
  methods: {
    async save(){
      if(this.setting.isGlobal && !this.setting.globalIp){
        this.globalIpRule=[ v => !!v || 'Please Input Global IP'];
        return;
      }
      this.$store.commit('changeLoading', true);
      axios.put(`setting`, this.setting).then(result => {
        this.$store.commit('changeLoading', false);
        if (result === 'successful') {
          this.$store.commit('changeSetting', this.setting);
          this.$toasted.show('Success!')
          this.$store.commit('changePingData',{});      
          if(this.setting.globalIp || this.setting.bacRestIp){
            axios.get('bac-rest/ping')
            .then(res=>{
                this.$store.commit('changePingData',res);
            });
            return;
          }
          
        }
      }).catch(({message}) => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(message, {type: 'error'})
      });
    },
  },
};
</script>

<style>
.card-height{
    height: 380px;
  }
  .p-relative{
    position: relative;
  }
  .required-noti {
    position: absolute;
    padding-left: 10px;
    bottom: 10px;
    left: 0;
    color: red;
    font-size: smaller;
  }
</style>



