<template>
  <v-row style="padding:20px">
    <v-col sm="3">
      <v-checkbox label="Points Service" v-model="setting.bacRestService" @change="save" />
    </v-col>
    <v-col sm="3">
      <v-checkbox label="BACnet Server Service" v-model="setting.bacnetService" @change="save" />
    </v-col>
    <v-col sm="3">
      <v-checkbox label="Rubix Wires Service" v-model="setting.wiresService" @change="save" />
    </v-col>
    <v-col sm="3">
      <v-checkbox label="Drac Service" v-model="setting.dracService" @change="save" />
    </v-col>
    <v-col sm="3">
      <v-checkbox label="BBB Service" v-model="setting.bbbService" @change="save" />
    </v-col>
    <v-col sm="3">
      <v-checkbox label="Lorawan Service" v-model="setting.lorawanService" @change="save" />
    </v-col>
    <v-col sm="3">
      <v-checkbox label="Mosquitto Service" v-model="setting.mosquittoService" @change="save" />
    </v-col>
    <v-col sm="3">
      <v-checkbox label="Platform Services Service" v-model="setting.smonService" @change="save" />
    </v-col>
    <v-col sm="3">
      <v-checkbox label="Networking" v-model="setting.networking" @change="save" />
    </v-col>
  </v-row>
</template>

<script>
import axios from "../axios";
export default {
  computed: {
    setting () {
      return this.$store.state.setting;
    },
    loading () {
      return this.$store.state.loading;
    }
  },
   methods:{
    async save(){
      this.$store.commit('changeLoading', true);
      axios.put(`setting`, this.setting).then(result => {
        this.$store.commit('changeLoading', false);
        if (result === 'successful') {
          this.$store.commit('changeSetting', this.setting);
          this.$toasted.show('Success!') 
        }
      }).catch(({message}) => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(message, {type: 'error'})
      });
    }
  }
};
</script>

<style>
</style>



